@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

 p {
	font-family: 'Press Start 2P', cursive;
	font-size: 12px;
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Pokedex-header {
display: inline-block;
}

.Pokedex-header > div {
    display: inline-block;
}
.title > p {
    margin: 10px;
    font-size: 50px;
    color: #FFDE00
}

.about {
position: relative;
}


.pokecard {
    margin: 1rem;
    box-shadow: 7px 10px 12px -5px rgba(0,0,0,0.56);
    padding: 10px;
    width: 200px;
    height: 180px;
    border-radius: 5px;
    background: #fcf8ec;
}

.pokecard-img {
    width: 60px;
    height: 60px;
    cursor: pointer;

}

.pokecard-img:hover {
    width: 90px;
    height: 90px;
}

.pokecard-title {
    font-weight: bolder;
    color: #383e56;
}

.pokecard-text p {
    margin: 0;
    font-size: 12px;
    font-weight: bolder;
    color: #383e56;
}

.pokecard-inner {
    display: inline;
}

.modal-body {
    font-family: 'Press Start 2P', cursive !important;
    background:  #FFDE00;
    color: #3B4CCA;
    border: #FF0000 solid 2px !important;
    border-radius: 2px;
}

.Pokedex-cards {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.Pokedex-buttons {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.Pokedex-buttons .search-bar {
  width: 350px;
}

 .btn-primary {
    margin-left: .2rem;
    margin-right: .2rem;
    background-color: #FFDE00 !important;
    border: none !important;
    font-weight: 500;
    font-family: 'Press Start 2P', cursive;
    font-size: 12px !important;
}

.custom-switch {
  padding-right: 11.8rem !important;
      font-family: 'Press Start 2P', cursive !important;
    font-size: 8.3px !important;
}

.custom-switch label {
padding-top: .4rem !important;
}

.btn-primary:hover, .btn-primary:active, .btn-primary:visited, .btn-primary:focus   {
    margin-left: 5px;
    background-color: #B3A125 !important;
    border: none !important;
}

[class='custom-control-input']:checked ~ [class='custom-control-label']::before {

        background-color: #FFDE00 !important;
        border-color: #FFDE00 !important;

}

.lds-circle {
    display: inline-block;
    -webkit-transform: translateZ(1px);
            transform: translateZ(1px);
  }
  .lds-circle > div {
    display: inline-block;
    width: 150px;
    height: 150px;
    margin: 8px;
    border-radius: 50%;
    color: linear-gradient(to bottom, #ff0000 50%, #eeeeee 50%);
    background: url(/pokedex-app/static/media/pokeball-icon.76612ad7.svg);
    -webkit-animation: lds-circle 2.4s cubic-bezier(0, 0.2, 0.6, 0.8) infinite;
            animation: lds-circle 2.4s cubic-bezier(0, 0.2, 0.6, 0.8) infinite;
    box-shadow: 7px 10px 12px -5px rgba(0,0,0,0.56);

  }
  @-webkit-keyframes lds-circle {
    0%, 100% {
      -webkit-animation-timing-function: cubic-bezier(1, 0, 2, 1);
              animation-timing-function: cubic-bezier(1, 0, 2, 1);
    }
    0% {
      -webkit-transform: rotateY(0deg);
              transform: rotateY(0deg);
    }
    50% {
      -webkit-transform: rotateY(1800deg);
              transform: rotateY(1800deg);
      -webkit-animation-timing-function: cubic-bezier(0, 1, 1, 2);
              animation-timing-function: cubic-bezier(0, 1, 1, 2);
    }
    100% {
      -webkit-transform: rotateY(3600deg);
              transform: rotateY(3600deg);
    }
  }
  @keyframes lds-circle {
    0%, 100% {
      -webkit-animation-timing-function: cubic-bezier(1, 0, 2, 1);
              animation-timing-function: cubic-bezier(1, 0, 2, 1);
    }
    0% {
      -webkit-transform: rotateY(0deg);
              transform: rotateY(0deg);
    }
    50% {
      -webkit-transform: rotateY(1800deg);
              transform: rotateY(1800deg);
      -webkit-animation-timing-function: cubic-bezier(0, 1, 1, 2);
              animation-timing-function: cubic-bezier(0, 1, 1, 2);
    }
    100% {
      -webkit-transform: rotateY(3600deg);
              transform: rotateY(3600deg);
    }
  }

.Nocontent {
    display: inline-block;
}

.Nocontent > div {
    display: inline-block;
    width: 30.3rem;
    height: 10rem;
    background-repeat: no-repeat;
    background: url(/pokedex-app/static/media/Pokemon.e31404a3.svg);
}
.Nocontent > p {
	font-family: 'Press Start 2P', cursive;
	font-size: 30px;
}
